.App {
  text-align: center;
  overflow: hidden;
}

a {
  text-decoration: none;
  color: white;
}

.arrows-link::before {
  content: '↘';
}

.arrows-link::after {
  content: '↙';
}

.arrows-link::before, a::after {
  position: relative;
  bottom: 6px;
  color: cornflowerblue;
}