body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overscroll-behavior-y: contain;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  overflow: hidden;
}

a {
  text-decoration: none;
  color: white;
}

.arrows-link::before {
  content: '\2198';
}

.arrows-link::after {
  content: '\2199';
}

.arrows-link::before, a::after {
  position: relative;
  bottom: 6px;
  color: cornflowerblue;
}
#page {
    position: relative;
    -webkit-transition: top 1s cubic-bezier(0.49, 0.29, 0.25, 1.18), -webkit-transform 2s;
    transition: top 1s cubic-bezier(0.49, 0.29, 0.25, 1.18), -webkit-transform 2s;
    transition: top 1s cubic-bezier(0.49, 0.29, 0.25, 1.18), transform 2s;
    transition: top 1s cubic-bezier(0.49, 0.29, 0.25, 1.18), transform 2s, -webkit-transform 2s;
    /* cubic-bezier(0.5, 1.45, 0.29, 0.75); */
    height: 100vh;
}

#up-arrow {
    -webkit-transform: scaleY(.2);
            transform: scaleY(.2);
}

.move-screen {
    -webkit-transform: perspective(2000px) rotateY(90deg);
            transform: perspective(2000px) rotateY(90deg);
}

#down-arrow {
    -webkit-transform: rotateX(180deg);
            transform: rotateX(180deg);
    -webkit-transform: scaleY(-.2);
            transform: scaleY(-.2);
}

#up-arrow, #down-arrow {
    cursor: pointer;
    position: relative;
    width: 15vw;
    min-width: 100px;
    -webkit-transition: opacity 1s, -webkit-transform .4s;
    transition: opacity 1s, -webkit-transform .4s;
    transition: opacity 1s, transform .4s;
    transition: opacity 1s, transform .4s, -webkit-transform .4s;
}

#up-arrow:hover {
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
}

#down-arrow:hover {
    -webkit-transform: scaleY(-1);
            transform: scaleY(-1);
}

.single-page {
    width: 86vw;
    height: 90vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    color: white;
    font-size: calc(10px + 2vmin);
    padding: 5vh 7vw;
}

body::after {
    width: 100vw;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -2;
    background-color: #282c34;
    content: '';
    background: -webkit-linear-gradient(139deg, #1a1b1b, #045283);
    background: linear-gradient(311deg, #1a1b1b, #045283);
    background-size: 400% 400%;

    -webkit-animation: scroll-background 13s ease infinite;

            animation: scroll-background 13s ease infinite;
}


@-webkit-keyframes scroll-background { 
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
}


@keyframes scroll-background { 
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
}

#up-container, #down-container {
    display: flex;
    justify-content: space-around;
    position: fixed;
    width: 100vw;
    left: 0;
    height: 10vh;
}

#up-container {
    top: 0;
}

#down-container {
    bottom: 0;
}
#top-content {
    display: flex;
    justify-content: space-around;
    width: 90%;
}

img {
    width: 100%;
    max-width: 50vw;
    margin: 10px;
}

.contents {
    display: flex;
    justify-content: space-around;
    flex-direction: column;
}

.hidden {
    display: none;
}
.nav {
  overflow: hidden;
  height:100%;
  border-radius: 100px;
  max-width: 15px;
  width: 5vw;
  padding: 1vw 4vw;
  cursor: pointer;
  z-index: 1;
}

.nav-pill {
  background-color: white;
  border: 1px solid black;
  height:100%;
  border-radius: 100px;
  max-width: 7px;
  width: 5vw;
  -webkit-transition: background-color 1s;
  transition: background-color 1s;
}

.selected > .nav-pill {
  background-color: crimson;
  -webkit-transition: background-color 1s;
  transition: background-color 1s;
}

.right-nav {
  position: fixed;
  right: 0;
  top: 0;
  height: 100vh;
  width: 4vw;
  display: flex;
  flex-direction: column;
  justify-content: stretch;
  align-items: center;
}
.single-skill {
  min-width: 128px;
}
.all-skills {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  width: 100%;
  overflow: auto;
  white-space: nowrap;
}
.contact {
  position: fixed;
  left: 0;
  bottom: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-left: 12px;
}

.contact > a {
  position: relative;
  right: 1vh;
  -webkit-transition: right 1s;
  transition: right 1s;
}

.contact.desktop {
  height: 128px;
}

.contact.mobile {
  flex-direction: column;
  width: 128px;
}

.contact img {
  height: 64px;
  width: 64px;
}

.contact.mobile.hidden > a {
  right: 100%;
  -webkit-transition: right 1s;
  transition: right 1s;
}

.contact.mobile > .toggler {
  width: 7vw;
  height: 7vh;
}

.contact.mobile > div.tint {
  width: 100vw;
  height: 100vh;
  left: 0;
  top: 0;
  position: fixed;
  z-index: -1;
  -webkit-transition: background-color 1s;
  transition: background-color 1s;  
  background-color: transparent;
  pointer-events: none;
  content: '';
}
.contact.mobile:not(.hidden) > div.tint {
  background-color: #222222cc;
  pointer-events: all;
}
.job {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin: 2.5% 0;
}

.details {
  text-align: left;
}

.dates {
  text-align: right;
}

.position {
  font-weight: bold;
}

.location {
  font-style: italic;
}

#history {
  width: 100%;
  flex: 1 1;
}
